import React, { Component } from 'react';
import styled, {css} from 'styled-components'
import Select from './Select.js'
import countryNames from './country-names'

function widthLessThan(width){
  return (...args) => css`@media screen and (max-width: ${width}px) {${css(...args)}}`
}

const SearchBarContainer = styled.div`
  position: sticky;
  top: 0px;
  display: flex;
  background: ${props => props.theme.primary};
  padding: ${props => props.isFocused ? '0px': '10px'};
  align-items: stretch;
  ${props => props.theme.shadow.high}
  &, > *, .spacer{
    ${props => props.theme.transition}
  }
  > svg {
    width: ${props => props.isFocused ? '0px' : '100px'};
    margin-right: ${props => props.isFocused ? '0px' : '10px'};
    ${widthLessThan(500)`
      width: 0px;
      margin-right: 0px;
    `}
  }
  ${widthLessThan(350)`
    padding: ${props => props.isFocused ? '0px': '5px'};
  `}  
`  
const SearchBox = styled.div`
  display: flex;
  border-radius: 0px 15px 15px 0px;
  width: 100%;
  font-size: 12px;
  ${props => props.theme.shadow.low}
  &:hover {
    ${props => props.theme.shadow.veryHigh}
  }
  > .person-selector{
    display: flex;
    width: calc(100% - 180px);
    > input {
      outline: none;
      border: none;
      flex-grow: 1;
      font-family: 'Roboto', sans-serif;
      height: 100%;
    }
    > button {
      display: inline-block;
      border: none;
      background: #fff;
      color: ${props => props.isFocused ? props.theme.primary : '#ccc'};
      height: 100%;
      outline: none;
    }
  }
  > .country-selector{
    height: 100%;
    width: 180px;
  }
  > .spacer {
    background: ${props => props.theme.secondary};
    width: ${props => props.isFocused ? '0px' : '15px'};
    border-radius: ${props => props.isFocused ? '0px' : '0px 15px 15px 0px'};
  }
  ${widthLessThan(500)`
    border-radius: 0px;
    > .spacer {
      width: 0px;
      border-radius: 0px;
    }    
  `}  

  ${widthLessThan(350)`
    flex-direction: column;
    > .person-selector, > .country-selector{
      height: 50%;
      width: 100%;
    }
  `}  
}
`
SearchBarContainer.defaultProps = {
  theme:{
    primary: "#000",
    secondary: "#888",
    shadow: {
      low: "box-shadow: none;",
      high: "box-shadow: none;",
      veryHigh: "box-shadow: none;",
    }
  }
}

